import "./styles/Digital.css";
import { Fade } from "react-reveal";
import React, { useState, useEffect } from "react";
import { StyledFlexBox, StyledBox } from "../Styles/Shared.styles";
import { flex } from "styled-system";

const Digital = () => {
  const [isScreenTooSmall, setIsScreenTooSmall] = useState(0);
  useEffect(() => {
    if (window.innerWidth <= 1024 && window.innerWidth > 768)
      setIsScreenTooSmall(1);
    else if (window.innerWidth < 768)
      setIsScreenTooSmall(2);
  }, []);
  return (
    <div className="digital">
      <div className="abs-curve-text">
        <div className="right-abs">
          <Fade left duration={500}>
            {isScreenTooSmall !== 1 &&
              <div className="list" style={{
                width: "70%"
              }}>
                <div className="box">
                  <img src="/assets/cir1.png" alt="" />
                  <p>富含可溶性纖維
                  </p>
                  <p>多種礦物質
                  </p>
                </div>
                <div className="box">
                  <img src="/assets/cir2.png" alt="" />
                  <p>產生飽腹感
                  </p>
                  <p>抑制食慾
                  </p>
                </div>
                <div className="box">
                  <img src="/assets/cir3.png" alt="" />
                  <p>含單元、多元不飽和脂肪酸和
                  </p>

                </div>
                <div className="box">
                  <img src="/assets/cir4.png" alt="" />
                  <p>加速燃燒卡路里
                  </p>

                </div>
                <div className="box">
                  <img src="/assets/cir5.png" alt="" />
                  <p>增進體適能
                  </p>
                </div>
              </div>
            }
          </Fade>
          <div style={{
            display: flex,
            justifyContent: "center"
          }}>


            <Fade left duration={500}>
              {isScreenTooSmall === 1 &&
                <>
                  <div className="list" style={{
                    width: "100%"
                  }}>
                    <div className="box">
                      <img src="/assets/cir1.png" alt="" />
                      <p>富含可溶性纖維
                      </p>
                      <p>多種礦物質
                      </p>
                    </div>
                    <div className="box">
                      <img src="/assets/cir2.png" alt="" />
                      <p>產生飽腹感
                      </p>
                      <p>抑制食慾
                      </p>
                    </div>
                    <div className="box">
                      <img src="/assets/cir3.png" alt="" />
                      <p>含單元、多元
                      </p>
                      <p>不飽和脂肪酸和
                      </p>
                    </div>
                  </div>
                  <div className="list" style={{
                    padding: "0 10rem",
                    width: "100%"
                  }}>
                    <div className="box">
                      <img src="/assets/cir4.png" alt="" />
                      <p>加速燃燒卡路里
                      </p>

                    </div>
                    <div className="box">
                      <img src="/assets/cir5.png" alt="" />
                      <p>增進體適能
                      </p>
                    </div>
                  </div>
                </>}
            </Fade>
          </div>
          <Fade bottom duration={500}>
            <img className="contextImg" src="/assets/content1.png" alt="" />
          </Fade>
          <Fade bottom duration={500}>
            <img className="contextImg" src="/assets/content2.png" alt="" />
          </Fade>
        </div>
      </div>
    </div>
  );
};

export default Digital;
