import "./styles/Products.css";
import { Link } from "react-router-dom";
import { Fade } from "react-reveal";
import { StyledFlexBox, StyledBox } from "../Styles/Shared.styles";
import React, { useState, useEffect } from "react";
import { fetchProduct, fetchOrder } from "../../Apis/User";
import { UPLOAD_URL } from "../../Constants";
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Drawer from '@mui/material/Drawer';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";

const Products = () => {
  const [isScreenTooSmall, setIsScreenTooSmall] = useState(0);
  const [data, setData] = useState([]);
  const [cart, setCart] = useState([]);
  const [cartDrawerOpen, setCartDrawerOpen] = useState(false);
  const [isCheckoutModalOpen, setIsCheckoutModalOpen] = useState(false);
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [bankCode, setBankCode] = useState("");
  const [orderDone, setOrderDone] = useState(false);
  const getCurrentDate = () => {
    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1; // 月份是從 0 開始的，所以要加 1
    const day = currentDate.getDate();

    // 使用 Intl.DateTimeFormat API 格式化日期
    const formattedDate = new Intl.DateTimeFormat('zh-TW', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(currentDate);

    return formattedDate;
  };

  useEffect(() => {
    if (window.innerWidth <= 1024 && window.innerWidth > 768)
      setIsScreenTooSmall(1);
    else if (window.innerWidth < 768)
      setIsScreenTooSmall(2);
    fetchProduct().then(({ data }) => {
      setData(data);
    });
  }, []);

  const addToCart = (product) => {
    const existingProductIndex = cart.findIndex(item => item.id === product.id);

    if (existingProductIndex !== -1) {
      // 商品已经存在于购物车中，将其数量加1
      const updatedCart = [...cart];
      updatedCart[existingProductIndex].quantity += 1;
      setCart(updatedCart);
    } else {
      // 商品不存在于购物车中，将其添加到购物车中
      setCart([...cart, { ...product, quantity: 1 }]);
    }
  };

  const increaseQuantity = (productId) => {
    const updatedCart = cart.map(item =>
      item.id === productId ? { ...item, quantity: item.quantity + 1 } : item
    );
    setCart(updatedCart);
  };

  const decreaseQuantity = (productId) => {
    const updatedCart = cart.map(item =>
      item.id === productId ? { ...item, quantity: Math.max(1, item.quantity - 1) } : item
    );
    setCart(updatedCart);
  };

  const calculateTotalPrice = () => {
    return cart.reduce((total, item) => total + item.price * item.quantity, 0);
  };

  const openCartDrawer = () => {
    setCartDrawerOpen(true);
  };

  const closeCartDrawer = () => {
    setCartDrawerOpen(false);
  };

  const openCheckoutModal = () => {
    setIsCheckoutModalOpen(true);
  };

  const closeCheckoutModal = () => {
    setIsCheckoutModalOpen(false);
    setOrderDone(false)
  };

  const Order = () => {
    // 您的下單邏輯在這裡，這裡只是一個範例
    fetchOrder({
      distributor_id: 3,
      name: name,
      address: address,
      phone: phone,
      email: email,
      bank_account: bankCode,
      order_details: cart.map(item => ({ product_id: item.id, quantity: item.quantity }))
    }).then(({ data }) => {
      if (data.status === 201) {
        setOrderDone(true)
      }
      // 這裡可以處理下單成功後的操作
    });
  };

  return (
    <div id="products" className="products">
      <Fade left>
        <div className="product-row">
          <StyledFlexBox width="100%" justifyContent="center">
            <StyledBox>
              <StyledFlexBox fontSize="36px" mt={4} style={{ fontWeight: 700 }} justifyContent="center">
                下單專區
              </StyledFlexBox>
              <StyledFlexBox fontSize="18px" pt={2} color="#505050" justifyContent="center">
                查看所有商品
              </StyledFlexBox>
              <StyledBox className="productList" pt={"4rem"} width="100%">
                {data.length > 0 && data.map((product, index) => (
                  <StyledBox key={index} className="productList-box">
                    <div>
                      <img style={{ width: "100%" }} src={UPLOAD_URL + product.image_path} alt="" />
                    </div>
                    <StyledBox className="productList-font" textAlign="center" style={{ fontWeight: 700 }}>
                      {product.name}
                    </StyledBox>
                    <StyledBox className="productList-font" textAlign="center" pt={2}>
                      NT${product.price}
                    </StyledBox>
                    <StyledBox textAlign="center" pt={2}>
                      <Button
                        className="productList-button"
                        variant="contained"
                        color="primary"
                        onClick={() => addToCart(product)}
                      >
                        加入購物車
                      </Button>
                    </StyledBox>
                  </StyledBox>
                ))}
              </StyledBox>
            </StyledBox>
          </StyledFlexBox>
        </div>
      </Fade>
      <Fade left>
        <div className="product-row">
          <StyledFlexBox width="100%" justifyContent="center" pt={4}>
            <img style={{ width: "100%" }} src={isScreenTooSmall === 0 ? "./assets/content3.png" : "./assets/content3-mobile.png"} alt="" />
          </StyledFlexBox>
        </div>
      </Fade>
      <div style={{ background: "#FFFFFF", display: "flex", justifyContent: "center", textAlign: "center", position: 'fixed', bottom: 40, right: 40, zIndex: 9999, border: "1px solid #1976d2", borderRadius: "50%", padding: "0.2rem" }}>
        <IconButton onClick={openCartDrawer}>
          <ShoppingCartIcon color="primary" style={{ width: "4rem", height: "4rem", maxWidth: "40px", maxHeight: "40px" }} />
          {cart.length > 0 && <div className="red-dot"></div>}
        </IconButton>
      </div>

      <Drawer anchor="right" open={cartDrawerOpen} onClose={closeCartDrawer}>
        <div style={{ width: '300px', padding: '16px' }}>
          <h2>購物車 ({cart.length})</h2>
          <ul>
            {cart.map((item, index) => (
              <StyledFlexBox justifyContent="center" alignItems="center" key={index}>
                <div style={{ width: "50%" }}>
                  <img style={{ width: "100%" }} src={UPLOAD_URL + item.image_path} alt="" />
                </div>
                <StyledBox>
                  <StyledBox className="productList-font" textAlign="center" style={{ fontWeight: 700 }}>
                    {item.name}
                  </StyledBox>
                  <StyledBox className="productList-font" textAlign="center" pt={2}>
                    NT${item.price}
                  </StyledBox>
                  <StyledBox className="productList-font" textAlign="center" pt={2}>
                    數量: {item.quantity}
                  </StyledBox>
                  <StyledBox className="productList-font" textAlign="center" pt={2}>
                    <Button variant="outlined" color="primary" onClick={() => decreaseQuantity(item.id)} style={{ width: "40%" }}>
                      -
                    </Button>
                    <Button variant="outlined" color="primary" onClick={() => increaseQuantity(item.id)} style={{ width: "20%" }}>
                      +
                    </Button>
                  </StyledBox>
                </StyledBox>
              </StyledFlexBox>
            ))}
          </ul>
          <StyledBox className="productList-font" textAlign="center" pt={2}>
            總價: NT${calculateTotalPrice()}
          </StyledBox>
          {calculateTotalPrice() > 0 && (
            <Button
              variant="contained"
              color="primary"
              onClick={openCheckoutModal}
            >
              結帳
            </Button>
          )}
          <Dialog open={isCheckoutModalOpen} onClose={closeCheckoutModal}>
            {orderDone === false ?
              <>
                <DialogTitle>結帳</DialogTitle>
                <DialogContent>
                  <DialogContentText>
                    <Typography variant="subtitle1">帳單資訊</Typography>
                    <TextField
                      label="名字"
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      required
                      error={name === ""}
                      helperText={name === "" ? "名字不能為空" : ""}
                    />
                    <TextField
                      label="地址"
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      value={address}
                      onChange={(e) => setAddress(e.target.value)}
                      required
                      error={address === ""}
                      helperText={address === "" ? "地址不能為空" : ""}
                    />
                    <TextField
                      label="電話"
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      required
                      error={!/^\d+$/.test(phone)}
                      helperText={!/^\d+$/.test(phone) ? "電話格式錯誤" : ""}
                    />
                    <TextField
                      label="信箱"
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      required
                      error={!/^\S+@\S+\.\S+$/.test(email)}
                      helperText={!/^\S+@\S+\.\S+$/.test(email) ? "信箱格式錯誤" : ""}
                    />
                    <TextField
                      label="銀行後五碼"
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      value={bankCode}
                      onChange={(e) => setBankCode(e.target.value)}
                      required
                      error={bankCode.length !== 5}
                      helperText={bankCode.length !== 5 ? "請輸入五個數字" : ""}
                    />
                    <Typography variant="subtitle1">您的訂單</Typography>
                    {cart.map((item, index) => (
                      <div key={index}>
                        <Typography>{item.name} × {item.quantity}</Typography>
                        <Typography>金額: NT${item.price * item.quantity}</Typography>
                      </div>
                    ))}
                    <Typography variant="subtitle1">總金額: NT${calculateTotalPrice()}</Typography>
                    <Typography variant="subtitle1">運費: NT${calculateTotalPrice() > 1000 ? "滿千免運" : "$60"}</Typography>
                    <Typography variant="subtitle1">總價: NT${calculateTotalPrice() > 1000 ? calculateTotalPrice() : (calculateTotalPrice() + 60)}</Typography>
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={Order}
                    disabled={name === "" || address === "" || !/^\d+$/.test(phone) || !/^\S+@\S+\.\S+$/.test(email) || bankCode.length !== 5}

                  >
                    結帳
                  </Button>
                  <Button onClick={closeCheckoutModal} color="primary">
                    關閉
                  </Button>
                </DialogActions>
              </> :
              <>
                <DialogTitle>訂單完成</DialogTitle>
                <DialogContent>
                  <DialogContentText>
                    <StyledFlexBox justifyContent="center">
                      <img style={{ width: "50%" }} src="./assets/chocolate-logo.jpg" alt="" />

                    </StyledFlexBox>
                    <Typography variant="subtitle1">謝謝，我們已經收到您的訂單。</Typography>

                    <Typography variant="body1">
                      <strong>日期:</strong> {getCurrentDate()}
                      <br />
                      <strong>電子郵件:</strong> {email}
                      <br />
                      <strong>總計:</strong> NT${calculateTotalPrice() > 1000 ? calculateTotalPrice() : (calculateTotalPrice() + 60)}
                      <br />
                      <strong>付款方式:</strong> 銀行轉帳
                      <br />
                      <br />
                      <strong>我們的銀行匯款資訊：</strong>
                      <br />
                      戶名: 張智凱
                      <br />
                      銀行: 永豐銀行008
                      <br />
                      帳戶號碼: 0240180031892
                      <br />
                      <br />
                    </Typography>
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={closeCheckoutModal} color="primary" variant="contained">
                    關閉
                  </Button>
                </DialogActions>

              </>}

          </Dialog>
        </div>
      </Drawer>
    </div>
  );
};

export default Products;
