import { service, getDataFromLocalStorage, setDataToLocalStorage, serviceVerify } from ".";

export const fetchProduct = (data) => {
  return new Promise((resolve, reject) => {
    service({
      url: "/product",
      method: "GET",
      data,
    })
      .then(async (res) => await resolve(res.data))
      .catch((error) => reject(error));
  });
};

export const fetchOrder = (data) => {
  return new Promise((resolve, reject) => {
    service({
      url: "/order",
      method: "POST",
      data,
    })
      .then(async (res) => await resolve(res))
      .catch((error) => reject(error));
  });
};